import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";

import Home from './Home';
import MyBookList from "./MyBookList";

const Routes = () => (
    <BrowserRouter>
        <Switch>
          <Route path="/interests">
            <Home />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
    </BrowserRouter>
)

export default Routes;